// fingo - main-info.sass
//
// * --------------------------------
// * RESET
// * --------------------------------
@import "./config/fingo-colors";
@import "./Modules/sass/style";


input, .nav {
  @extend %reset-border-box; }
input {
  @extend %reset-input; }
// reset
button {
  @extend %reset-button; }

// body
//// overflow-y: hidden
// .movie-info-on:checked ~ body
//   overflow-y: none


.dim-movie-info {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  @include size(100% 100vh);
  overflow: scroll;
  // overflow-y: hidden
  z-index: 1000; }

.dim-movie-info.active {
  display: block; }
.dim-movie-bg {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  @include background(color rgba(#161616, 0.7));
  z-index: 1000; }
.movie-info {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10000;
  box-shadow: 0px 0px 30px #161616;
  // overflow: scroll
  overflow: hidden;
  border-radius: 10px;

  @include size(940px auto);
  @include background(color #222);
  @include margin(top 50px bottom 50px left auto right auto);
  .steel-cut-carousel {
    @include size(940px 300px);
    position: relative;
    .ui-carousel-button-group {
      @include size(940px 300px);
      position: absolute;
      button::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        transition: all 0.2s ease-in-out;
        z-index: 111; }
      .ui-carousel-prev-button::before {
        top: 50%;
        left: 4%;
        transform: translateY(-50%) rotate(-45deg);
        border-top: 3px solid #8b8b8b;
        border-left: 3px solid #8b8b8b; }
      .ui-carousel-next-button::before {
        top: 50%;
        right: 4%;
        transform: translateY(-50%) rotate(135deg);
        border-top: 3px solid #8b8b8b;
        border-left: 3px solid #8b8b8b; } }
    .steel-cut {
      // +padding(top 300px)
      position: relative;
      .steel-cut-list {
        overflow: hidden;
        @include position(absolute, top 0 left 0);
        @include size(100% 300px);
        img {
          @include size(940px auto); } } } }
  .movie-section-01 {
    // background-color: #111
    @include size(640px 250px);
    @include margin(left 270px);
    h1 {
      @include padding(top 25px bottom 5px);
      @include margin(bottom 20px);
      font-size: 26px;
      color: $fingo-font-dd;
      font-weight: bold;
      border-bottom: 1px solid #333; }
    >p {
      font-size: 14px;
      color: $fingo-font-99; }
    .poster {
      @include size(220px 320px);
      border: 3px solid #222;
      @include position(absolute, top 210px left 30px);
      img {
        @include size(220px 320px); } } }
  section {
    h2 {
      font-size: 14px;
      color: $fingo-font-dd;
      font-weight: bold;
      @include margin(bottom 10px); } }
  .movie-section-02 {
    float: left;
    @include size(630px 185px);
    @include padding(top 25px left 30px right 30px);
    // background-color: #111
    border-top: 1px solid #333;

    p {
      font-size: 12px;
      color: $fingo-font-99; } }
  .movie-section-03 {
    @include padding(left 30px top 30px);
    box-sizing: border-box;
    border-top: 1px solid #333;
    border-left: 1px solid #333;
    float: right;
    @include size(250px 100%);
    div {
      div {
        img {
          @include size(50px 70px);
          border-radius: 5px; } } }
    // +position(absolute, top 550px right 0)
    div {
      @include margin(bottom 40px);
      img {
        float: left;
        @include margin(right 15px); }
      p {
        color: $fingo-font-dd;

        font-size: 14px;
        color: $fingo-font-dd; } }
    div::after {
      content: "";
      display: block;
      clear: both; } }
  .movie-section-04 {
    clear: both;
    position: relative;
    top: -850px;
    @include padding(left 30px top 50px);

    h2 {
      @include margin(bottom 30px); }
    >div {
      // background-color: #111
      @include size(630px auto);
      @include margin(bottom 20px);
      .profile {
        height: 100px;
        // background-color: #000
        float: left;
        img {
          float: left;
          @include margin(right 15px); } }
      p {
        display: inline-block; }
      .user-name {
        @include margin(right 5px);
        font-size: 14px;
        color: $fingo-font-dd; }
      // .rating-stars
      //   font-size: 12px
      //   color: $fingo-mian-color
      //   display: inline-block
      //   +margin(right 5px)
      //span
      .comment {
        font-size: 14px;
        color: $fingo-font-dd;
        display: block; }
      time {
        font-size: 12px;
        color: $fingo-font-99; } }
    button {
      @include size(640px 40px);
      @include background(color $fingo-bg-dark);
      font-size: 14px;
      color: $fingo-font-dd; }
    >div::after {
      content: "";
      display: block;
      clear: both; } } }
// .rating-stars
//   +margin(top 5px)
.rating-stars {
  font-size: 12px;
  color: $fingo-mian-color;
  display: inline-block;
  @include margin(top 5px);
  span {
    @include margin(right 3px); }
  // span:last-child
  //   display: inline-block
 }  //   +margin(top -2px)
.hover:hover {
  opacity: 1; }
.hover {
  @include position(absolute, top 0 z-index 10);
  @include size(100% 100%);
  opacity: 0;
  cursor: pointer;
  background: rgba(#000000, 0.3);
  text-align: center;
  p {
    @include margin(top 55px);
    font-size: 16px;
    color: $fingo-white;
    @include mobile {
      font-size: 20px;
      @include margin(top 90px); } }


  .like-btn,
  .heart-btn {
    @include margin(bottom 10px left auto right auto);
    @include size(110px 35px);
    font-size: 14px;
    display: block;
    color: #fff;
    border: 1px solid currentColor;
    letter-spacing: -1px;
    @include mobile {
      @include size(180px 50px);
      font-size: 16px; }
    img:first-child {
      @include size(16px 13px);
      @include margin(right 5px);
      @include mobile {
        @include size(21px 19px); } }



    &:nth-of-type(1):hover,
    &:nth-of-type(1):active {
      @include background(color $fingo-heart);
      border: 1px solid $fingo-heart; }
    &:nth-of-type(2):hover,
    &:nth-of-type(2):active {
      @include background(color #27569c);
      border: 1px solid #27569c; } } }
